.usa-tooltip__body--top {
  transition: opacity 0.08s ease-in-out;
  background-color: #1b1b1b;
  border-radius: 0.25rem;
  color: #f3f3f3;
  display: none;
  font-size: 0.94rem;
  padding: 0.5rem;
  pointer-events: none;
  width: auto;
  white-space: pre;
  z-index: 100000;
  position: absolute;
