.usa-tooltip__body--top:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  pointer-events: none;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid #1b1b1b;
  position: absolute;
  bottom: -5px;
  left: 50%;
  margin-left: -5px;
}

