  border-bottom: 5px solid transparent;
  border-right: 5px solid #1b1b1b;
  border-left: 0;
  right: auto;
  top: 50%;
  bottom: 0;
  left: -5px;
  margin: -5px 0 0 0;
}

.usa-tooltip__body--left:after {
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid #1b1b1b;
  border-right: 0;
  right: -5px;
